import React,{useState} from 'react'
import {Link} from 'react-router-dom';
import avatar from '../assets/avatar.png';
import { makeStyles } from '@material-ui/core/styles';
import MobileRightMenuSlider from '@material-ui/core/Drawer';
import{
    AppBar,Toolbar,ListItem,IconButton,ListItemText,ListItemIcon,Avatar,Divider,List,Typography,Box
} from '@material-ui/core';

import{
    ArrowBack,AssignmentInd,Home,Apps,ContactMail
}from '@material-ui/icons';
import Footer from './Footer';

const useStyles= makeStyles(theme=>({
    menuSliderContainer:{
        width:'250',
        background:'#511',
        height:'100%'
        
    },
    avatar:{
        display:'block',
        margin:'0.5rem auto',
        width: theme.spacing(13),
        height:theme.spacing(13)
    },
    listItem:{
        color:'tan'
    }
}))

const menuItems=[
    {
        listIcon:<Home/>,
        listText:'Home',
        listPath:'/'
    },
    {
        listIcon:<AssignmentInd/>,
        listText:'Resume',
        listPath:'/resume'
    },
    {
        listIcon:<Apps/>,
        listText:'Portfolio',
        listPath:'/portfolio' 
    },
    {
        listIcon:<ContactMail/>,
        listText:'Contact',
        listPath:'/contact'
    },
]

const Navbar = () => {
    const classes=useStyles();

    const [state,setState]=useState({
        right:false
    })
    const toggleSlider=(slider,open)=>()=>{
        setState({...state,[slider]:open});
    };

    const sideList=slider=>(
        <Box 
            className={classes.menuSliderContainer} 
            component='div'
            onCLick={toggleSlider(slider,false)}
            >
            <Avatar className={classes.avatar} src={avatar} alt='Jatin Malhotra'/>
            <Divider/>
            <List>
                {menuItems.map((lsItem,key)=>(
                <ListItem button key={key} component={Link} to={lsItem.listPath}>
                    <ListItemIcon className={classes.listItem} >{lsItem.listIcon}</ListItemIcon>
                    <ListItemText className={classes.listItem} primary={lsItem.listText}/>
                </ListItem>
                ))}
            </List>
        </Box>
    )
    return (
        <div>
            <Box component='nav' >
            <AppBar postion='static' style={{background:'#222'}}>
                <Toolbar>
                    <IconButton onClick={toggleSlider('right',true)}>
                        <ArrowBack style={{color:'tomato'}}/>
                    </IconButton>
                    <Typography variant='h5' style={{color:'tan'}} >
                        Portfolio 
                    </Typography>
                    <MobileRightMenuSlider 
                       anchor='right' 
                       open={state.right}
                       onClose={toggleSlider('right',false)}
                       >
                        {sideList('right')}
                        <Footer/>
                    </MobileRightMenuSlider>
                </Toolbar>
            </AppBar>
        </Box>
        </div>
    )
}
/* */
export default Navbar
